html{
  max-width: 100%;
  overflow-x: hidden;
  background: #28c1ba;
}

body {
  max-width: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  font-weight: 300;
  line-height: 24px;

  background: -webkit-radial-gradient(rgb(25, 15, 39) 0%,#402d5A 100%);

  background: -o-radial-gradient(rgb(25, 15, 39) 0%,#402d5A 100%);

  background: radial-gradient(rgb(25, 15, 39) 0%,#402d5A 100%); 
  color: white;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 400;
}



